body {
  margin: 0;
  padding: 0;
  font-family: Lato, sans-serif;
  font-size: 16px;
}

* {
  box-sizing: border-box;
}

button {
  cursor: pointer;
}

button:focus {
  outline: none;
}

a {
  text-decoration: none;
  cursor: pointer;
}

a:hover a:focus,
a:visited {
  text-decoration: none;
  outline: none;
}

@font-face {
  font-family: 'Lato';
  src: url("../fonts/Lato-Regular.eot");
  src: url("../fonts/Lato-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Lato-Regular.woff2") format("woff2"), url("../fonts/Lato-Regular.woff") format("woff"), url("../fonts/Lato-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
  text-rendering: optimizeLegibility;
}

@font-face {
  font-family: 'Roboto Regular';
  src: url("../fonts/Roboto-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
  text-rendering: optimizeLegibility;
}

@font-face {
  font-family: 'Roboto Conrensed';
  src: url("../fonts/Roboto-Conrensed.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
  text-rendering: optimizeLegibility;
}

.popup {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
  flex-flow: row nowrap;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.3);
  width: 100%;
  height: 100%;
  z-index: 100;
}

.popup__inner {
  width: 50%;
  max-width: 450px;
  background-color: #fff;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
  height: auto;
  border: 1px solid #b7c2d9;
  border-radius: 8px;
}

.popup__header {
  font-family: "Roboto Regular", sans-serif;
  font-weight: 600;
  border-radius: 8px 8px 0 0;
  font-size: 1.2em;
  padding: 20px 50px;
  color: #8791a4;
  background-color: #f4f5f7;
  text-transform: capitalize;
}

.popup__form {
  font-family: "Roboto Regular", sans-serif;
  font-weight: 600;
  padding: 20px 50px;
}

.form {
  text-align: left;
}

.form__item {
  margin-bottom: 15px;
}

.form__input {
  font-family: "Roboto Conrensed", sans-serif;
  font-weight: 400;
  width: 100%;
  border: 1px solid #e1e6f0;
  color: #8791a4;
  border-radius: 3px;
  font-size: 0.8em;
  background: #fff;
  padding: 10px 7px 10px 10px;
}

.form__input:focus {
  border: 1px solid #1385c7;
  outline: none;
}

.form__label {
  display: block;
}

.form__title {
  font-size: 0.8em;
  color: #1385c7;
  margin-bottom: 5px;
}

.form__btn {
  margin-top: 15px;
  position: relative;
  overflow: hidden;
  z-index: 2;
  width: 150px;
  height: 45px;
  background-color: #1385c7;
  border: 1px solid #1385c7;
  cursor: pointer;
  color: #fff;
  text-transform: uppercase;
}

.form__btn span {
  display: inline-block;
  font-family: "Roboto Regular", sans-serif;
  font-weight: 600;
  color: #fff;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

.form__btn:before {
  content: "";
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  line-height: 40px;
  font-size: 1.3em;
  top: 0;
  left: -100%;
  -webkit-transition: left 0.3s;
  -o-transition: left 0.3s;
  transition: left 0.3s;
}

.form__btn:hover:before {
  left: 0;
}

.form__btn:hover span {
  -webkit-transform: translateX(150px);
  -o-transform: translateX(150px);
  transform: translateX(150px);
}